/* FONTS */
/* COLORS */
/* GLOBAL */
html {
  font-size: 100% !important; }

body {
  line-height: calc(1.4 * 1.2rem) !important;
  font-size: 1.2rem !important;
  font-weight: 300 !important; }

strong, b {
  font-style: normal;
  font-weight: 700; }

em, i {
  font-style: italic; }

h1, .fusion-page-title-bar h1
h1.entry-title .post-content h1
.fusion-title h1 {
  font-size: 2.1em !important;
  font-weight: 600 !important; }

/* MENU */
.fusion-main-menu > ul > li > a {
  text-transform: uppercase;
  font-weight: 600 !important; }

/* HOME */
.home #content {
  margin-left: 0; }

.hero-text-overlay {
  background-color: #F7E599;
  bottom: 60px; }

.homepage-contentbox-bkgrd {
  width: 100%;
  max-width: 100%;
  height: 254px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 0; }

.homepage-contentboxes .cimacs-bkgrd {
  background-image: url("./images/home_panel_cancer_immune_monitoring.jpg"); }

.homepage-contentboxes .cidc-bkgrd {
  background-image: url("./images/home_panel_cancer_immunologic_commons.jpg"); }

.fusion-content-boxes.content-boxes-clean-horizontal.homepage-contentboxes .content-container {
  margin-bottom: 0; }

.fusion-content-boxes.content-boxes-clean-horizontal.homepage-contentboxes .col {
  padding: 0;
  border: #1C65BE 2px solid; }

.fusion-content-boxes.content-boxes-clean-horizontal.homepage-contentboxes .heading {
  background-color: #1C65BE;
  padding: 0.7rem 5.5rem; }

.fusion-content-boxes.content-boxes-clean-horizontal.homepage-contentboxes .fusion-column {
  padding-left: 30px;
  padding-right: 30px; }

.homepage-news-column {
  text-align: center; }
  .homepage-news-column .homepage-news-content {
    font-family: "ff-good-web-pro" !important;
    font-weight: 300;
    line-height: 140%;
    font-size: 1.2em !important;
    width: 60%;
    margin: 0 auto; }
    .homepage-news-column .homepage-news-content strong {
      font-weight: 600; }

#main .fusion-content-boxes.content-boxes-clean-horizontal.homepage-contentboxes h2 {
  font-size: 1.3em !important;
  line-height: 110% !important;
  font-family: "ff-good-web-pro" !important;
  font-weight: 600;
  text-align: center; }

.fusion-content-boxes.content-boxes-icon-on-side.homepage-contentboxes .heading {
  margin-bottom: 0; }

/* ALL PAGES */
#content {
  margin-left: 100px; }

.fusion-page-title-bar {
  border-top: #0B9FEC 10px solid !important; }
  .fusion-page-title-bar .fusion-page-title-wrapper {
    align-items: flex-end; }

/* LANDING PAGE */
.landing-page-title-bar.fusion-page-title-bar {
  border: none !important;
  height: 200px;
  background-position: 100% 0 !important;
  background-color: #98D3F2;
  margin-bottom: 60px; }
  .landing-page-title-bar.fusion-page-title-bar .fusion-page-title-wrapper h1.entry-title {
    text-transform: uppercase;
    max-width: 500px; }

/* FOOTER */
.upper-footer-margin {
  margin-top: 55px; }

.footer-inst-logos {
  width: 660px;
  margin: 20px auto 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-bottom: 40px;
  border-bottom: #fff 1px solid; }

.footer-menu-and-legal {
  text-align: center;
  color: #fff; }

.footer-menu-and-legal a {
  text-decoration: underline;
  color: #fff;
  font-weight: 400; }

.footer-menu-and-legal a:hover {
  color: #ccc; }

.footer-menu-and-legal .footer-menu {
  display: inline-block; }

.footer-menu-and-legal .footer-menu ul {
  list-style: none; }

.footer-menu-and-legal .footer-menu li {
  float: left;
  padding: 0 10px;
  border-right: #fff 1px solid; }

.footer-menu-and-legal .footer-menu li:last-child {
  border-right: none;
  padding-right: 0; }

#footer .footer-menu-and-legal .footer-menu a {
  font-size: 0.9em; }

#footer .footer-menu-and-legal .footer-legal {
  font-size: 0.8em; }

#footer .footer-menu-and-legal .footer-legal a {
  font-size: 1.0em; }

/* UTILTIES */
.absolute {
  position: absolute; }

.relative {
  position: relative; }

@media (max-width: 768px) {
  .footer-inst-logos {
    width: 100%;
    flex-direction: column;
    align-content: center; }
    .footer-inst-logos a {
      flex: 100%;
      margin: 20px 0; } }
